import React from 'react';
import {HashRouter, Switch, Route} from 'react-router-dom';
import PageScrollTop from './components/pageToTop/PageScrollTop'

// pages import Here 
import RkHome from "./pages/RkHome";
import RkAboutRookie from "./pages/RkAboutRookie";
import RkBusiness from "./pages/RkBusiness";
import RkToken from "./pages/RkToken";
import RkDAO from "./pages/RkDAO";
import RkRoadmap from "./pages/RkRoadmap";
import RkMembers from "./pages/RkMembers";
import RkFounders from "./pages/RkFounders";
import RkAdvisory from "./pages/RkAdvisory";
import RkPartner from "./pages/RkPartner";
import RkFaq from "./pages/RkFaq";
import RkContactUs from "./pages/RkContactUs";

// elements import Here 
import Error from "./pages/Error";

// import Css Here 
import './assets/scss/style.scss';

const App = () => {
    return (
        <HashRouter>
            <PageScrollTop>
                <Switch>

                    <Route path={`${process.env.PUBLIC_URL + "/"}`} exact component={RkHome}/>
                     {/**/}
                    <Route path={`${process.env.PUBLIC_URL + "/about-rookie"}`} exact component={RkAboutRookie}/>
                    <Route path={`${process.env.PUBLIC_URL + "/business"}`} exact component={RkBusiness}/>
                    <Route path={`${process.env.PUBLIC_URL + "/token"}`} exact component={RkToken}/>            
                    <Route path={`${process.env.PUBLIC_URL + "/dao"}`} exact component={RkDAO}/>  
                    <Route path={`${process.env.PUBLIC_URL + "/roadmap"}`} exact component={RkRoadmap}/>  
                    {/**/}
                    <Route path={`${process.env.PUBLIC_URL + "/founders"}`} exact component={RkFounders}/>
                    <Route path={`${process.env.PUBLIC_URL + "/members"}`} exact component={RkMembers}/>                    
                    <Route path={`${process.env.PUBLIC_URL + "/advisory"}`} exact component={RkAdvisory}/>  
                    <Route path={`${process.env.PUBLIC_URL + "/partner"}`} exact component={RkPartner}/>  
                    {/**/}
                    <Route path={`${process.env.PUBLIC_URL + "/faq"}`} exact component={RkFaq}/>
                    <Route path={`${process.env.PUBLIC_URL + "/contact-us"}`} exact component={RkContactUs}/> 
                    {/**/}
                    <Route path={`${process.env.PUBLIC_URL + "/error"}`} exact component={Error}/>

                </Switch>
            </PageScrollTop>
        </HashRouter>
    )
}


export default App
